import React from "react"

import image1 from "../images/projects/operations/homepage.png"
import image2 from "../images/projects/operations/tech-portal.png"
import image3 from "../images/projects/operations/tech-forms.png"
import image4 from "../images/projects/operations/record-summary.png"
import image5 from "../images/projects/operations/record-table.png"

import { FaArrowCircleLeft, FaArrowCircleRight } from "react-icons/fa"
import { Fade } from "react-slideshow-image"
import "react-slideshow-image/dist/styles.css"
import SEO from "../components/seo"
import Layout from "../components/layout"

const fadeImages = [image1, image2, image3, image4, image5]

const properties = {
  prevArrow: (
    <FaArrowCircleLeft
      style={{ width: 50, height: 90, marginRight: 30, cursor: "pointer" }}
    />
  ),
  nextArrow: (
    <FaArrowCircleRight
      style={{ width: 50, height: 90, marginLeft: 30, cursor: "pointer" }}
    />
  ),
}

const atsOperations = () => {
  return (
    <Layout>
      <div className="slide-container">
        <SEO title="Projects" />
        <Fade {...properties}>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[0]} alt="sc 1" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[1]} alt="sc 2" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[2]} alt="sc 3" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[3]} alt="sc 4" />
            </div>
          </div>
          <div className="each-fade">
            <div className="image-container">
              <img src={fadeImages[4]} alt="sc 5" />
            </div>
          </div>
        </Fade>

        <div
          style={{
            fontSize: 25,
            marginBottom: 20,
            color: "#c8d5c3",
          }}
        >
          Advance Telematics Solutions Operations System (Veda)
        </div>
        <div>
          This was my biggest project while I was working in ATS. This is a
          web-based application that the comapany uses to manage and handle
          transaction records mainly GPS devices installations and removals of
          ATSUAE. The application also has a separate portal for the technicians
          for them to be able to send vehicle job details. Most of the forms of
          the company like Leave, Overtime, etc forms are also accesible within
          the application.
        </div>
      </div>
    </Layout>
  )
}

export default atsOperations
